import Component from "vue-class-component";
import axios from "axios";
import * as indexApi from "../../api/indexApi";

@Component({
  props: {},
  components: {
    // categoryView,
  }
})
export default class AddGoods extends Vue {

//   ORDER_INIT,//初始化状态
// ORDER_WAIT_PAY,//等待支付
// ORDER_PAY_ONDEV,//货到付款
// ORDER_CANCEL_PAY,//取消支付或者支付失败
// ORDER_WAIT_SENDGOODS,//等待发货
// ORDER_WAIT_RECVGOODS,//待收货
// ORDER_END_GOODS//完成

handleOrderStatus(status){
switch(status){
  case 'ORDER_INIT':
  return '订单初始化';
   case 'ORDER_WAIT_PAY':
  return '等待支付';
   case 'ORDER_PAY_ONDEV':
  return '货到付款';
   case 'ORDER_CANCEL_PAY':
  return '取消支付';
   case 'ORDER_WAIT_SENDGOODS':
  return '待发货';
     case 'ORDER_WAIT_RECVGOODS':
  return '待收货';
  case 'ORDER_END_GOODS':
  return '完成';
  default :
  return '';
}
}

  allprescription = 0;
  allPrescription() {
    indexApi.allPrescription().then(res => {
      if (res["retCode"]) {
        console.log(res.data);
        this.allprescription = res.data;
      } else {
        if(!res['islogin']){this.$alert(res["message"]);}
        console.error("数据查询错误");
      }
    });
  }
  page = 0;
  pageSize = 10;
  total = 100;
  onPageChange(page) {
    this.page = page - 1;
    this.getOrderList();
  }
  orderList = [];
  getOrderList() {
    indexApi
      .prescriptionList({
        page: this.page,
        pageSize: this.pageSize
      })
      .then(res => {
        if (res["retCode"]) {
          this.orderList = res.data.list;
          this.total = res.data.page.total;
        } else {
          if(!res['islogin']){this.$alert(res["message"]);}
          console.error("数据查询错误");
        }
      });
  }

  mounted() {
    this.allPrescription();
    this.getOrderList();
  }
}



